import React, {Component} from 'react';
import {observer} from "mobx-react";
import store from "../Store";
import {removeObservable} from "../helpers/entity/mobx";
import socket from "../services/socketio";
import constant from "../constants";
import {isEmpty} from "../helpers/utility";

const ClientListener = constant.ClientListener;
const ServerListener = constant.ServerListener;
const ClientConstant = constant.Client;
const RoomConstant = constant.Room;

const XX = [-1, -1, -1, 1, 1, 1, 0, 0];
const YY = [-1, 0, 1, -1, 0, 1, -1, 1];

function evaluateMove(board, chessman, reverseChessman, x, y, xMove, yMove) {
    let count = 0;
    let x1 = x + xMove;
    let y1 = y + yMove;
    while (true) {
        if (!(0 <= x1 && x1 < 8 && 0 <= y1 && y1 < 8)) {
            return false;
        }

        if (board[x1][y1] == reverseChessman) {
            count++;
        } else {
            if (board[x1][y1] == chessman) {
                break;
            } else {
                return false;
            }

        }
        x1 = x1 + xMove;
        y1 = y1 + yMove;
    }

    if (count > 0) {
        return true;
    }

    return false;

}

function reverseCell(board, chessman, reverseChessman, x, y, xMove, yMove) {
    console.log(xMove + ",", yMove);
    let x1 = x + xMove;
    let y1 = y + yMove;
    while (true) {

        if (!(0 <= x1 && x1 < 8 && 0 <= y1 && y1 < 8)) {
            return;
        }

        if (board[x1][y1] == reverseChessman) {
            console.log(board);
            board[x1][y1] = chessman;
        } else {
            if (board[x1][y1] == chessman) {
                break;
            } else {
                return;
            }

        }
        x1 = x1 + xMove;
        y1 = y1 + yMove;
    }
}

@observer
class ListRoom extends Component {
    constructor(props) {
        super(props);
    }

    componentWillMount() {
        socket.on(ClientListener.JOINED_GAME, (room) => {
            store.updateRoom(room);
        });

        socket.on(ClientListener.UPDATE_BOARD, (room) => {
            store.updateRoom(room);
        });
    }


    onClickCell = (x, y, move) => {
        store.getCurrentRoom.board[x][y] = store.player.chessman;

        move.forEach((data) => {
            reverseCell(store.getCurrentRoom.board, store.player.chessman, store.partner.chessman, x, y, data.xx, data.yy);
        });

        socket.emit(ServerListener.TICK, store.getCurrentRoom.board);

    };

    createFences = () => {
        let fences = removeObservable(store.getCurrentRoom.board);
        console.log(fences);
        for (let i = 0; i < 8; i++) {
            for (let j = 0; j < 8; j++) {
                if (fences[i][j] == 1 || fences[i][j] == 2) {
                    for (let k = 0; k < 8; k++) {
                        if (0 <= i + XX[k] && i + XX[k] < 8 && 0 <= j + YY[k] && j + YY[k] < 8 && !fences[i + XX[k]][j + YY[k]]) {
                            fences[i + XX[k]][j + YY[k]] = -1;
                        }
                    }

                }
            }
        }

        return fences;
    }

    isAvailableMove = (fences, board, chessman) => {
        let moves = new Array(8).fill(null).map(() => new Array(8));
        let reverseChessman;
        if (chessman == 1) {
            reverseChessman = 2;
        } else {
            reverseChessman = 1;
        }

        for (let i = 0; i < 8; i++) {
            for (let j = 0; j < 8; j++) {
                if (fences[i][j] == -1) {
                    for (let k = 0; k < 8; k++) {
                        let isEvaluateMove = evaluateMove(board, chessman, reverseChessman, i, j, XX[k], YY[k]);
                        if (isEvaluateMove) {
                            fences[i][j] = -2;
                            if (moves[i][j] == undefined) {
                                moves[i][j] = [];
                            }
                            moves[i][j].push({xx: XX[k], yy: YY[k]});
                        }
                    }
                }
            }
        }
        return moves;
    }

    renderBoard = () => {
        let fences = this.createFences();
        const moves = this.isAvailableMove(fences, store.getCurrentRoom.board, store.player.chessman);
        console.log(store.player.chessman);
        return (
            <div className="board-game">
                {
                    store.getCurrentRoom.board.map((row, indexX) => {
                        return (
                            <div className="board-row" key={indexX}>
                                {
                                    row.map((cell, indexY) => {
                                        return (
                                            <div
                                                className="cell-game"
                                                key={indexY}
                                                onClick={() => {
                                                    if (moves[indexX][indexY] && store.yourTurn) {
                                                        this.onClickCell(indexX, indexY, moves[indexX][indexY]);
                                                    }
                                                }}>
                                                {
                                                    cell == RoomConstant.Board.Cell.BLACK ?
                                                        <div className="dot-black"/> :
                                                        cell == RoomConstant.Board.Cell.WHITE ?
                                                            <div className="dot-white"/> :
                                                            <div
                                                                className={(moves[indexX][indexY] && store.yourTurn ?
                                                                    (store.player.chessman == RoomConstant.Board.Cell.BLACK ?
                                                                        "hover-black" : "hover-white") : "not-allowed")}/>
                                                }

                                            </div>
                                        )
                                    })
                                }
                            </div>
                        )

                    })
                }
            </div>
        )
    };

    render() {
        const room = store.getCurrentRoom;

        if (room.status == RoomConstant.Status.PLAYING) {
            return this.renderBoard();
        }

        return <div className="waiting-partner">Đang đợi đối thủ...</div>;
    }
}

export default ListRoom;
