import React, {Component} from 'react';
import {observer} from "mobx-react";
import store from "../Store";
import {Col, Row} from 'antd';
import socket from "../services/socketio";
import constant from "../constants";
import {isEmpty} from "../helpers/utility";

const ClientListener = constant.ClientListener;
const ServerListener = constant.ServerListener;
const RoomConstant = constant.Room;

@observer
class ListRoom extends Component {

    componentWillMount() {
        socket.on(ClientListener.UPDATE_ROOM, (room) => {
            store.updateRoom(room);
        });
        socket.on(ClientListener.JOINED_ROOM, (room) => {
            store.updateRoom(room);
            store.setCurrentRoom(room);
        });
        // socket.on(ClientListener.OUT_ROOM, (room) => {
        //     store.updateRoom(room);
        // });
    }

    joinRoom = (room) => {
        console.log(room);
        socket.emit(ServerListener.JOIN_ROOM, {roomId: room.id});
    };

    countPlayer = (room) => {
        let count = 0;
        if (!isEmpty(room.playerWhite)) {
            count++;
        }
        if (!isEmpty(room.playerBlack)) {
            count++;
        }
        return count;
    };

    renderRoom = (room) => {
        const numberPlayer = this.countPlayer(room);
        return (
            <Col span={4}>
                <div className="room" onClick={() => {
                    if (room.status != 2) this.joinRoom(room)
                }}>
                    <div className={"room-empty " + `room-${room.status}-user`}>
                        <div className="text">
                            {numberPlayer}/2
                        </div>
                    </div>
                    <div className="room-name">
                        Phòng {room.id}
                    </div>
                </div>

            </Col>
        )
    }

    render() {
        return (
            <div>

                <Row gutter={24}>
                    {
                        store.roomsStore.map((room, index) => {
                            return this.renderRoom(room);
                        })
                    }
                </Row>
            </div>

        );
    }
}

export default ListRoom;
