import React, {Component} from 'react';
import {Modal} from "antd";
import Form from "./common/Form";
import FormInput from "./common/FormInput";
import FormButton from "./common/FormButton";
import {observer} from "mobx-react";
import store from "../Store";
import socket from "../services/socketio";
import constant from "../constants";

const ClientListener = constant.ClientListener;
const ServerListener = constant.ServerListener;

@observer
class ModalFormName extends Component {

    submitModalName = (data) => {
        socket.emit(ServerListener.LOG_IN, data);
        socket.on(ClientListener.LOG_IN_SUCCESS, (dataSocket) => {
            store.submitModalName(dataSocket);
        });

    };

    render() {
        return (
            <Modal
                title="Điền tên của bạn"
                visible={store.visibleModalName}
                footer={null}
                closable={false}
            >
                <Form onSubmit={this.submitModalName}>
                    <FormInput
                        name="name"
                        suffixClear
                        placeholder="Nhập tên của bạn"
                        rules={[
                            {
                                required: true,
                                message: "Vui lòng nhập tên của bạn!"
                            }
                        ]}

                    />
                    <div style={{display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                        <FormButton
                            type="primary"
                            htmlType="submit"
                        >
                            Đồng ý
                        </FormButton>
                    </div>
                </Form>
            </Modal>
        );
    }
}

export default ModalFormName;
